import service from "@/uilt/request"

/**
 * 
 * @param {} page 
 * @param {} limit 
 * @param {} state 
 * @param {} orderType 
 */
export function orderList(page,limit,userid,type)
{
    return service.get("/api/orders/getOrder",{
        params:{
            userid,
            page,
            limit,
            type
        }
    });
}
/**
 * 
 */
export function kclist(page,limit,lat,lon)
{
    return service.get("/api/orders/kclist",{
        params:{
            lat,lon,limit,page
        }
    });
}

/**
 * 
 * @param {} obj 
 */
 export function orderCreate(obj){

    return service.post("/api/orders",{
        data:obj
    });
 }

 /**
  * 
  * @param {}}} id 
  */
 export function DleteOder(id){
     return service.get("/api/orders/"+id);
 }
 
 /**
  * 
  * @param {,} userid 
  */
 export function compeleteOrder(userid)
 {
    return service.get("/api/orders/"+id);
 }
 
 /**
  * 
  * @param {} userid 
  */
 export function NotCompelateOrder(userid){
     return service.get("",{

     });
 }

 /**
  * 
  * @param {} obj 
  */
export function orderPrice(obj){
    return service.post("/api",{

    });
}

/**
 * 
 * @param {id} orderid 
 * @param {} state 
 */
export function orderState(orderid,state){
    return service.put("/api/orders/"+orderid,{
        state:state
    });
}

export function orderStateOfobj(orderid,obj){
    return service.put("/api/orders/"+orderid,obj);
}

/**
 * 
 * @param {openid}} wxopenid 
 * @param {}} msg 
 */
export function orderMsg(wxopenid,msg)
{
    service.post("/api/Token/SendMessage?openid="+wxopenid,{
        message: msg
    });
}

/**
 * 
 * @param {} wxopenid 
 * @param {} msg 
 */
export function orderComfMessage(wxopenid,msg)
{
    service.post("/api/",{
        wxopenid,msg
    })
}

/**
 * 
 */
export function userNowPrice(){
    service.post("/api/",{

    });
}

/**
 * 
 * @param {} page
 * @param {} limit
 * @param {id} userid
 * @param {}} state 
 */
export function driverOrder(page,limit,userid,state){
    return service.get("/api/orders/driverOrder",{
        params:{
            page,limit,userid,state
        }
    });
}
/**
 * 
 * 
 * @param {}} obj 
 */
export function complate(obj)
{
    return service.put("/orderComplete?id=" + obj.id,{
        state:1
    })
}
export function recOrder(id,obj){
    return service.put("/api/orders/meetOrder?id="+id,
        obj
    );
}




